@font-face {
  font-family: Avenir;
  src: url("Avenir.8bcf8c72.ttc");
}

@font-face {
  font-family: Avenir-Book;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir-Heavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

body {
  margin: 0;
  padding: 0 8px;
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

input {
  border: none;
  padding: 0;
}

.search-form {
  box-sizing: border-box;
  width: 100%;
  margin-top: 20px;
  display: flex;
}

.search-form__label {
  width: 100%;
  display: flex;
  position: relative;
}

.label-lg:before, .label-sm:before {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 19px;
  height: 19px;
  position: absolute;
  top: 25px;
  left: 26px;
}

.label-sm:before {
  width: 11px;
  height: 11px;
  position: absolute;
  top: 15px;
  left: 13px;
}

.search-form__bar {
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-family: Avenir, sans-serif;
}

.search-form__bar::placeholder {
  color: #3d4e61;
}

.search-form__bar:focus::placeholder {
  font-family: Avenir-Heavy, sans-serif;
}

.search-form__bar--lg {
  padding: 23px 26px 23px 62px;
  font-size: 16px;
  line-height: 22px;
}

.search-form__bar--sm {
  padding: 10px 13px 10px 33px;
  font-size: 14px;
  line-height: 20px;
}

.search-form__bar:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

